
.main {
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
}
.math {
  position: absolute;
  top: 25%;
  height: auto;
  font-size: 725%;
  color: #2c3e50;
  text-shadow: 0px 2px 3px #555;
}
#neg {
  left: 25%;
  background: #b30707;
  width: 1.5em;
  height: 1.5em;
  border-radius: 50%;
  margin: .25em;
  border-top: 0.05em solid #fa8c8c;
  border-bottom: 0.05em solid #510303;
}
#neg:hover, #neg:active {
    color: #fff;
    background: #cc0808;
}
#neg .label {
    width: 300px;
    height: 300px;
    bottom: -3em;
}
#pos {
  right: 25%;
  background: #0ea541;
  width: 1.5em;
  height: 1.5em;
  border-radius: 50%;
  margin: .25em;
  border-top: 0.05em solid #8af5ae;
  border-bottom: 0.05em solid #06471c;
}
#pos:hover, #pos:active {
    background: #10bd4a;
    color: #fff;
}
#pos .label {
    bottom: -3em;
}



